.card {
	border-radius: 1rem;
	box-sizing: border-box;
	margin: 2px;
	align-items: center;
	flex-direction: row;
}

.overlay {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0.3);
	z-index: 1000;
}
.pop-up {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: #fff;
	padding: 50px;
	z-index: 1000;
	border: 1px solid black;
	box-sizing: border-box;
	border-radius: 12px;
	padding: 2em;
	max-width: 600px;
	min-width: 500px;
}
